// =============================================================================
// X402 ROUTER - External API Layer for SMF
// =============================================================================
// Provides a single entry point for external clients (agents, MCP servers, etc.)
// to execute 402-protected API calls through NexFlow's SMF infrastructure.
//
// This module:
// - Accepts routing requests with preferences and constraints
// - Applies policy checks (spending limits, agent quotas, etc.)
// - Selects optimal facilitator using SMF routing logic
// - Executes the x402 payment flow and returns the API response

import { createLogger } from '@/lib/logger';
import { getFacilitatorRouter, type RoutingPreferences, type FacilitatorStrategy } from '@/integrations/x402/facilitators/facilitator-router';
import { getMetaFacilitator } from '@/integrations/x402/orchestrator/meta-facilitator';
import type { PaymentRequirements, IFacilitator } from '@/integrations/x402/facilitators/base-facilitator';
import { createRoute, createRouteAttempt, updateRouteStatus } from '@/db/smf';
import type { Erc8004Metadata } from '@/lib/nexflow-types';
import type { FacilitatorId } from '@/integrations/x402/facilitators/facilitator-registry';

const logger = createLogger({ component: 'X402Router' });

// =============================================================================
// Utility Functions
// =============================================================================

/**
 * Convert a maxSpend value to atomic units (BigInt)
 * 
 * Handles both human-readable decimals (e.g., "0.05" USDC) and 
 * atomic unit strings (e.g., "50000").
 * 
 * USDC has 6 decimals, so:
 * - "0.05" -> 50000n
 * - "1.0" -> 1000000n
 * - "50000" -> 50000n (already atomic)
 * 
 * @param value - The maxSpend string value
 * @param decimals - Token decimals (default 6 for USDC)
 * @returns BigInt atomic units, or null if invalid
 */
function toAtomicUnits(value: string | undefined, decimals: number = 6): bigint | null {
  if (!value) return null;
  
  try {
    // If it contains a decimal point, it's human-readable
    if (value.includes('.')) {
      const [whole, fraction = ''] = value.split('.');
      const paddedFraction = fraction.padEnd(decimals, '0').slice(0, decimals);
      const atomicString = whole + paddedFraction;
      return BigInt(atomicString);
    }
    
    // Otherwise assume it's already atomic units
    return BigInt(value);
  } catch (e) {
    logger.warn({ value, error: e instanceof Error ? e.message : 'Unknown', msg: 'Failed to convert maxSpend to atomic units' });
    return null;
  }
}

// =============================================================================
// Per-Facilitator Logging
// =============================================================================

/**
 * Agent type for categorizing traffic sources
 */
export type AgentType = 'dogfood' | 'scout' | 'external' | 'unknown';

/**
 * Log a routing decision with full facilitator context
 * 
 * Structured log entry for aggregation and AI-optimized SMF training.
 * Includes facilitatorId, facilitatorUrl, network, asset, mode, and decision reasons.
 * 
 * These logs are designed to be persisted to Supabase/Postgres for:
 * - NexFlow-native metrics aggregation
 * - SMF training and optimization
 * - Dashboard analytics
 */
export function logRoutingDecision(params: {
  facilitatorId: FacilitatorId | string;
  facilitatorUrl?: string;
  network: string;
  asset: string;
  mode: 'demo' | 'live';
  strategy: FacilitatorStrategy;
  agentId?: string;
  agentType?: AgentType;
  useCase?: string;
  resource?: string;
  serverId?: string;
  reasons?: string[];
  score?: number;
  alternatives?: number;
  erc8004Score?: number;
}): void {
  logger.info({
    component: 'X402Router',
    event: 'routing_decision',
    // Core identifiers
    facilitatorId: params.facilitatorId,
    facilitatorUrl: params.facilitatorUrl,
    network: params.network,
    asset: params.asset,
    // Mode and strategy
    mode: params.mode,
    strategy: params.strategy,
    // Agent labeling (critical for NexFlow-native metrics)
    agentId: params.agentId,
    agentType: params.agentType ?? 'unknown',
    useCase: params.useCase,
    // Resource/path info
    resourceUrl: params.resource,
    serverId: params.serverId,
    // Scoring
    reasons: params.reasons,
    score: params.score,
    alternatives: params.alternatives,
    erc8004Score: params.erc8004Score,
    msg: `Routing decision: ${params.facilitatorId} for ${params.network}`,
  });
}

/**
 * Log a route completion with outcome details
 * 
 * Structured log entry for tracking success/failure per facilitator.
 * Used for health scoring and AI-optimized facilitator selection.
 * 
 * Key fields for NexFlow-native metrics:
 * - agentType: dogfood | scout | external | unknown
 * - useCase: optional use-case tag (e.g., "demo", "prod-test", "merchant-alpha")
 * - status: derived from success boolean
 * - latencyMs: critical for p95 calculations
 * - reasons: same array from routing decision
 */
export function logRouteComplete(params: {
  facilitatorId: FacilitatorId | string;
  facilitatorUrl?: string;
  network: string;
  asset: string;
  success: boolean;
  errorCode?: string;
  errorMessage?: string;
  httpStatus?: number;
  latencyMs?: number;
  amountPaid?: string;
  transactionHash?: string;
  mode: 'demo' | 'live';
  agentType?: AgentType;
  useCase?: string;
  resourceUrl?: string;
  serverId?: string;
  reasons?: string[];
}): void {
  const logFn = params.success ? logger.info.bind(logger) : logger.warn.bind(logger);
  
  // Derive status for metrics
  const status = params.success ? 'success' : (params.errorCode === 'TIMEOUT' ? 'timeout' : 'failed');
  
  logFn({
    component: 'X402Router',
    event: 'route_complete',
    // Core identifiers
    facilitatorId: params.facilitatorId,
    facilitatorUrl: params.facilitatorUrl,
    network: params.network,
    asset: params.asset,
    // Outcome
    success: params.success,
    status, // normalized status for aggregation
    errorCode: params.errorCode,
    errorMessage: params.errorMessage,
    httpStatus: params.httpStatus,
    latencyMs: params.latencyMs,
    // Payment details
    amountPaid: params.amountPaid,
    transactionHash: params.transactionHash,
    // Mode
    mode: params.mode,
    // Agent labeling (critical for NexFlow-native metrics)
    agentType: params.agentType ?? 'unknown',
    useCase: params.useCase,
    // Resource info
    resourceUrl: params.resourceUrl,
    serverId: params.serverId,
    // Scoring context
    reasons: params.reasons,
    msg: params.success 
      ? `Route completed: ${params.facilitatorId} on ${params.network}` 
      : `Route failed: ${params.facilitatorId} - ${params.errorCode}`,
  });
}

