/**
 * Input for routeAndExecuteX402Job
 */
export interface RouteAndExecuteX402JobInput {
  agentId?: string;
  walletId?: string;
  resource: string;
  method: 'GET' | 'POST' | 'PUT' | 'DELETE';
  headers?: Record<string, string>;
  body?: unknown;
  maxSpend?: string;
  preferredNetworks?: string[];
  preferredAssets?: string[];
  facilitatorHints?: string[];
  /** Optional ERC-8004 metadata for the target action/resource */
  erc8004?: Erc8004Metadata;
  /** 
   * Facilitator strategy for explicit routing
   * - "auto": SMF chooses optimal facilitator (default)
   * - "cdp": Force Coinbase Developer Platform
   * - "payai": Force PayAI facilitator
   * - "x402rs": Force X402rs (Rust community facilitator)
   */
  facilitatorStrategy?: FacilitatorStrategy;
  /** Mode indicator for logging (demo vs live transactions) */
  mode?: 'demo' | 'live';
  /** 
   * Agent type for categorizing traffic sources
   * - "dogfood": Internal NexFlow dogfood agent
   * - "scout": NexFlow scout agent exploring under-used routes
   * - "external": External client/agent
   * - "unknown": Unknown source (default)
   */
  agentType?: AgentType;
  /**
   * Optional use-case tag for finer-grained analytics
   * Examples: "demo", "prod-test", "merchant-alpha", "integration-test"
   */
  useCase?: string;
}

/**
 * Result of routeAndExecuteX402Job
 */
export type RouteAndExecuteX402JobResult =
  | {
      status: 'success';
      routeId: string;
      facilitatorId: string;
      network: string;
      asset: string;
      amountPaid: string;
      httpStatus: number;
      httpHeaders: Record<string, string>;
      httpBody: unknown;
    }
  | {
      status: 'error';
      errorCode:
        | 'policy_blocked'
        | 'routing_failed'
        | 'facilitator_error'
        | 'network_error'
        | 'invalid_request'
        | 'internal_error';
      message: string;
      details?: unknown;
    };

/**
 * Policy check result
 */
interface PolicyCheckResult {
  allowed: boolean;
  reason?: string;
  details?: unknown;
}

/**
 * Facilitator selection result
 */
interface FacilitatorSelection {
  facilitatorId: string;
  scheme: string;
  network: string;
  asset: string;
  facilitator: IFacilitator;
}

// =============================================================================
// Network-to-Facilitator Mapping
// =============================================================================

/**
 * Get default facilitator for a given network
 * 
 * This helper provides a sensible default facilitator for each network.
 * Used when facilitatorStrategy is "auto" and no other routing decision
 * factors are available.
 * 
 * TODO: Evolve this to a score-based choice using health metrics,
 * cost, and historical performance data for AI-optimized SMF routing.
 * 
 * @param networkId - CAIP-2 network identifier (e.g., "eip155:8453")
 * @returns Default facilitator ID for the network
 */
export function getDefaultFacilitatorForNetwork(networkId: string): FacilitatorId {
  // Network-specific defaults
  switch (networkId) {
    // Base mainnet - X402rs for experimentation, CDP for production
    // TODO: Use score-based selection once we have enough health data
    case 'eip155:8453':
      // Prefer X402rs for Base mainnet to experiment with community facilitator
      // Fall back to CDP if X402RS_ENABLED is false
      if (process.env.X402RS_PREFER_BASE === 'true') {
        return 'x402rs';
      }
      return 'cdp';
    
    // Base Sepolia (testnet) - X402rs is a good choice
    case 'eip155:84532':
      return 'x402rs';
    
    // XDC mainnet - Only X402rs supports this
    case 'xdc:50':
      return 'x402rs';
    
    // Ethereum mainnet - PayAI has good multi-chain support
    case 'eip155:1':
      return 'payai';
    
    // BNB Chain - PayAI supports this
    case 'eip155:56':
      return 'payai';
    
    // Polygon - PayAI supports this
    case 'eip155:137':
      return 'payai';
    
    // Solana - CDP is the primary option
    case 'solana:mainnet':
    case 'solana:5eykt4UsFv8P8NJdTREpY1vzqKqZKvdp':
      return 'cdp';
    
    // Default to CDP for unknown networks
    default:
      return 'cdp';
  }
}

// =============================================================================
// ERC-8004 Trust Scoring
// =============================================================================

/**
 * Score ERC-8004 trust metadata for routing decisions
 * 
 * This is a placeholder scoring function that normalizes reputation scores
 * and applies tier-based boosts. The scoring is read-only and logged for
 * future tuning - it does not dramatically affect routing decisions yet.
 * 
 * Scoring logic:
 * - Base score: reputationScore (0-1 range, default 0)
 * - Tier boost: high +0.2, medium +0.1, low/unknown +0
 * - Final score capped at 1.0
 * 
 * @param meta - Optional ERC-8004 metadata from the action
 * @returns Normalized trust score between 0 and 1
 */
export function scoreErc8004Trust(meta?: Erc8004Metadata): number {
  if (!meta) return 0;

  // Base score from reputation (default to 0 if not present)
  const base = meta.reputationScore ?? 0;

  // Tier-based boost
  const tierBoost =
    meta.trustTier === 'high' ? 0.2 :
    meta.trustTier === 'medium' ? 0.1 :
    0;

  // Cap at 1.0
  return Math.min(1, base + tierBoost);
}

/**
 * Compute composite routing score including ERC-8004 trust
 * 
 * This function integrates ERC-8004 trust scoring into the overall
 * routing decision. Currently, 8004 trust has a low weight (0.1) to
 * minimize impact while we gather data for tuning.
 * 
 * @param baseScore - Base routing score (e.g., from facilitator health)
 * @param erc8004Meta - Optional ERC-8004 metadata
 * @returns Composite score with 8004 trust component
 */
export function computeCompositeRoutingScore(
  baseScore: number,
  erc8004Meta?: Erc8004Metadata
): { compositeScore: number; erc8004Score: number; erc8004Weight: number } {
  const erc8004Score = scoreErc8004Trust(erc8004Meta);
  const erc8004Weight = 0.1; // Low weight for now - tunable later
  
  // Composite: 90% base score + 10% ERC-8004 trust
  const compositeScore = (1 - erc8004Weight) * baseScore + erc8004Weight * erc8004Score;
  
  return {
    compositeScore,
    erc8004Score,
    erc8004Weight,
  };
}

