  //   const agentDailyLimit = await getAgentDailyLimit(agentId);
  //   if (agentSpendToday + BigInt(maxSpend || '0') > agentDailyLimit) {
  //     return { allowed: false, reason: 'Agent daily spending limit exceeded' };
  //   }
  // }
  //
