  // }
  //
  // 4. Max spend cap (safety limit):
  const MAX_SINGLE_SPEND = BigInt('100000000'); // 100 USDC in atomic units (6 decimals)
  const maxSpendAtomic = toAtomicUnits(maxSpend);
