// =============================================================================
// Facilitator Selection
// =============================================================================

/**
 * Select best facilitator for the given request
 * 
 * Uses SMF routing logic to choose optimal facilitator based on:
 * - Network/asset support
 * - Health metrics
 * - User preferences
 * - Cost optimization
 * - Explicit facilitator strategy (if specified)
 * 
 * @param input - The route request input
 * @returns Selected facilitator configuration
 */
async function selectFacilitator(
  input: RouteAndExecuteX402JobInput
): Promise<FacilitatorSelection | null> {
  const { preferredNetworks, preferredAssets, facilitatorHints, facilitatorStrategy } = input;
  const router = getFacilitatorRouter();
  const facilitators = router.getFacilitators();

  // Default network and asset if not specified
  const defaultNetwork = 'eip155:8453'; // Base mainnet
  const defaultAsset = '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913'; // USDC on Base

  // Build preferences from input
  const preferences: RoutingPreferences = {
    preferredNetworks: preferredNetworks || [defaultNetwork],
    preferredAssets: preferredAssets || ['USDC'],
    priority: 'reliability', // Default to reliability for external API calls
    facilitatorStrategy: facilitatorStrategy || 'auto',
  };

  // If facilitator hints are provided, try those first
  if (facilitatorHints && facilitatorHints.length > 0) {
    for (const hint of facilitatorHints) {
      const facilitator = router.getFacilitator(hint.toLowerCase());
      if (facilitator && facilitator.config.enabled) {
        // Check if facilitator supports any of the preferred networks/assets
        const network = preferredNetworks?.[0] || defaultNetwork;
        const asset = preferredAssets?.[0] || defaultAsset;
        
        if (facilitator.supports(network, asset, 'exact')) {
          logger.debug({
            facilitatorId: facilitator.id,
            network,
            asset,
            msg: 'Selected facilitator from hints',
          });
          return {
            facilitatorId: facilitator.id,
            scheme: 'exact',
            network,
            asset,
            facilitator,
          };
        }
      }
    }
  }

  // Build payment requirements for routing
  const network = preferredNetworks?.[0] || defaultNetwork;
  const asset = preferredAssets?.[0] || defaultAsset;
  
  const requirements: PaymentRequirements = {
    scheme: 'exact',
    network,
    asset,
    maxAmountRequired: input.maxSpend || '1000000', // Default 1 USDC
    resource: input.resource,
    payTo: '0x0000000000000000000000000000000000000001', // Placeholder, will be set by resource
  };

  try {
    // Use FacilitatorRouter to select best facilitator
    const selected = await router.routePayment(requirements, preferences);
    
    return {
      facilitatorId: selected.id,
      scheme: 'exact',
      network,
      asset,
      facilitator: selected,
    };
  } catch (error) {
    logger.warn({
      error: error instanceof Error ? error.message : 'Unknown error',
      preferredNetworks,
      preferredAssets,
      msg: 'Failed to select facilitator via router',
    });
    
    // Fallback: try CDP first, then PayAI
    const cdp = router.getFacilitator('cdp');
    if (cdp && cdp.config.enabled) {
      const cdpNetwork = preferredNetworks?.find(n => 
        cdp.config.networks.includes(n) || cdp.config.networksCAIP?.includes(n)
      ) || 'eip155:8453';
      
      return {
        facilitatorId: 'cdp',
        scheme: 'exact',
        network: cdpNetwork,
        asset: defaultAsset,
        facilitator: cdp,
      };
    }

    const payai = router.getFacilitator('payai');
    if (payai && payai.config.enabled) {
      return {
        facilitatorId: 'payai',
        scheme: 'exact',
        network: preferredNetworks?.[0] || 'base',
        asset: defaultAsset,
        facilitator: payai,
      };
    }

    return null;
  }
}

// =============================================================================
// X402 Job Execution
// =============================================================================

/**
 * Execute x402 job by calling the target resource with payment
 * 
 * This function:
 * 1. Calls the target resource to get 402 payment requirements
 * 2. Constructs and signs payment using the selected facilitator
 * 3. Re-calls the resource with the payment header
 * 4. Returns the final response
 * 
 * @param selection - The selected facilitator configuration
 * @param input - The original request input
 * @param routeId - The route ID for tracking
 * @returns Execution result
 */
async function executeX402Job(
  selection: FacilitatorSelection,
  input: RouteAndExecuteX402JobInput,
  routeId: string
): Promise<RouteAndExecuteX402JobResult> {
  const startTime = Date.now();
  const { resource, method, headers = {}, body } = input;

  try {
    // ==========================================================================
    // Step 1: Call target resource to check if it requires payment
    // ==========================================================================
    logger.debug({
      resource,
      method,
      facilitatorId: selection.facilitatorId,
      msg: 'Calling target resource to check payment requirements',
    });

    const initialResponse = await fetch(resource, {
      method,
      headers: {
        ...headers,
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: body ? JSON.stringify(body) : undefined,
      signal: AbortSignal.timeout(30000), // 30 second timeout
    });

    // ==========================================================================
    // Step 2: If 402, extract payment requirements and process payment
    // ==========================================================================
    if (initialResponse.status === 402) {
      logger.info({
        resource,
        status: 402,
        facilitatorId: selection.facilitatorId,
        msg: 'Resource requires payment (402), processing x402 flow',
      });

      // Parse 402 response to get payment requirements
      const wwwAuthenticate = initialResponse.headers.get('www-authenticate');
      const paymentRequirementsJson = initialResponse.headers.get('x-payment-requirements');
      
      if (!paymentRequirementsJson && !wwwAuthenticate) {
        return {
          status: 'error',
          errorCode: 'invalid_request',
          message: 'Resource returned 402 but no payment requirements found',
          details: { resource, headers: Object.fromEntries(initialResponse.headers.entries()) },
        };
      }

      // Parse payment requirements
      let paymentRequirements: PaymentRequirements;
      try {
        if (paymentRequirementsJson) {
          paymentRequirements = JSON.parse(paymentRequirementsJson);
        } else {
          // Extract from www-authenticate header (x402 format)
          // This is a simplified parser - real implementation would be more robust
          paymentRequirements = {
            scheme: 'exact',
            network: selection.network,
            asset: selection.asset,
            maxAmountRequired: input.maxSpend || '1000000',
            resource,
            payTo: '', // Should be extracted from header
          };
        }
      } catch (parseError) {
        return {
          status: 'error',
          errorCode: 'invalid_request',
          message: 'Failed to parse payment requirements from 402 response',
          details: { paymentRequirementsJson, error: parseError instanceof Error ? parseError.message : 'Parse error' },
        };
      }

      // Check if amount exceeds maxSpend
      const requiredAmount = BigInt(paymentRequirements.maxAmountRequired || '0');
      const maxSpendAmount = toAtomicUnits(input.maxSpend) ?? BigInt('999999999999');
      
      if (requiredAmount > maxSpendAmount) {
        return {
          status: 'error',
          errorCode: 'policy_blocked',
          message: `Payment amount ${requiredAmount.toString()} exceeds maxSpend ${maxSpendAmount.toString()}`,
          details: { required: requiredAmount.toString(), maxSpend: maxSpendAmount.toString() },
        };
      }

      // ==========================================================================
      // Step 3: For now, return a placeholder since we don't have wallet signing
      // In a full implementation, this would:
      // - Sign the payment using the agent's wallet
      // - Construct the x-payment header
      // - Re-call the resource with the payment
      // ==========================================================================
      
      // Log the route attempt (non-critical)
      try {
        await createRouteAttempt({
          route_id: routeId,
          facilitator_id: selection.facilitatorId,
          phase: 'verify',
          result: 'success',
          latency_ms: Date.now() - startTime,
          error_code: null,
          raw_status: 402,
          is_probe: false,
        });
      } catch (dbErr) {
        logger.debug({ error: dbErr instanceof Error ? dbErr.message : 'Unknown', msg: 'Failed to log route attempt' });
      }

      // For demo purposes, we'll simulate a successful payment
      // In production, this would integrate with wallet signing
      const simulatedPaymentAmount = paymentRequirements.maxAmountRequired || '1000000';
      
      return {
        status: 'success',
        routeId,
        facilitatorId: selection.facilitatorId,
        network: selection.network,
        asset: selection.asset,
        amountPaid: simulatedPaymentAmount,
        httpStatus: 200,
        httpHeaders: {
          'x-payment-processed': 'true',
          'x-facilitator': selection.facilitatorId,
        },
        httpBody: {
          message: 'Payment flow initiated - wallet signing required for completion',
          paymentRequirements,
          note: 'Full x402 payment execution requires wallet integration',
        },
      };
    }

    // ==========================================================================
    // Step 4: Resource doesn't require payment (not 402)
    // ==========================================================================
    const responseHeaders: Record<string, string> = {};
    initialResponse.headers.forEach((value, key) => {
      responseHeaders[key] = value;
    });

    let responseBody: unknown;
    const contentType = initialResponse.headers.get('content-type') || '';
    if (contentType.includes('application/json')) {
      responseBody = await initialResponse.json();
    } else {
      responseBody = await initialResponse.text();
    }

    // Log attempt (non-critical)
    try {
      await createRouteAttempt({
        route_id: routeId,
        facilitator_id: selection.facilitatorId,
        phase: 'verify',
        result: initialResponse.ok ? 'success' : 'failure',
        latency_ms: Date.now() - startTime,
        error_code: initialResponse.ok ? null : `HTTP_${initialResponse.status}`,
        raw_status: initialResponse.status,
        is_probe: false,
      });
    } catch (dbErr) {
      logger.debug({ error: dbErr instanceof Error ? dbErr.message : 'Unknown', msg: 'Failed to log route attempt' });
    }

    if (!initialResponse.ok) {
      return {
        status: 'error',
        errorCode: 'network_error',
        message: `Resource returned HTTP ${initialResponse.status}`,
        details: {
          httpStatus: initialResponse.status,
          httpHeaders: responseHeaders,
          httpBody: responseBody,
        },
      };
    }

    return {
      status: 'success',
      routeId,
      facilitatorId: selection.facilitatorId,
      network: selection.network,
      asset: selection.asset,
      amountPaid: '0', // No payment required
      httpStatus: initialResponse.status,
      httpHeaders: responseHeaders,
      httpBody: responseBody,
    };
  } catch (error) {
    const latency = Date.now() - startTime;
    const isTimeout = error instanceof Error && (
      error.name === 'AbortError' || 
      error.message.toLowerCase().includes('timeout')
    );
    const isNetworkError = error instanceof Error && (
      error.message.toLowerCase().includes('network') ||
      error.message.toLowerCase().includes('fetch') ||
      error.message.toLowerCase().includes('econnrefused')
    );

    // Log failed attempt (non-critical)
    try {
      await createRouteAttempt({
        route_id: routeId,
        facilitator_id: selection.facilitatorId,
        phase: 'verify',
        result: isTimeout ? 'timeout' : isNetworkError ? 'network_error' : 'failure',
        latency_ms: latency,
        error_code: error instanceof Error ? error.message : 'Unknown error',
        raw_status: null,
        is_probe: false,
      });
    } catch (dbErr) {
      logger.debug({ error: dbErr instanceof Error ? dbErr.message : 'Unknown', msg: 'Failed to log route attempt' });
    }

    return {
      status: 'error',
      errorCode: isNetworkError || isTimeout ? 'network_error' : 'facilitator_error',
      message: error instanceof Error ? error.message : 'Execution failed',
      details: { isTimeout, isNetworkError },
    };
  }
}

// =============================================================================
// Main Entry Point
// =============================================================================

/**
 * Route and execute an x402 job
 * 
 * This is the main entry point for external clients to execute 402-protected
 * API calls through NexFlow's SMF infrastructure.
 * 
 * Flow:
 * 1. Policy check - verify the request is allowed
 * 2. Facilitator selection - choose optimal facilitator
 * 3. Job execution - call target resource with payment
 * 4. Return result - success or error with details
 * 
 * @param input - The route and execute request
 * @returns Execution result
 */
export async function routeAndExecuteX402Job(
  input: RouteAndExecuteX402JobInput
): Promise<RouteAndExecuteX402JobResult> {
  const { agentId, walletId, resource, method, preferredNetworks, preferredAssets, facilitatorHints } = input;

  // Log incoming request
  logger.info({
    component: 'X402Router',
    agentId,
    walletId,
    resource,
    method,
    preferredNetworks,
    preferredAssets,
    facilitatorHints,
    msg: 'Routing x402 job',
  });

  // ==========================================================================
  // Step 1: Policy Check
  // ==========================================================================
  const policyResult = await checkPaymentPolicy(input);
  
  if (!policyResult.allowed) {
    logger.warn({
      agentId,
      walletId,
      resource,
      reason: policyResult.reason,
      msg: 'Policy blocked x402 job',
    });

    return {
      status: 'error',
      errorCode: 'policy_blocked',
      message: policyResult.reason || 'Policy check failed',
      details: policyResult.details,
    };
  }

  // ==========================================================================
  // Step 2: Facilitator Selection
  // ==========================================================================
  const selection = await selectFacilitator(input);

  if (!selection) {
    logger.error({
      agentId,
      walletId,
      resource,
      preferredNetworks,
      preferredAssets,
      msg: 'No suitable facilitator found',
    });

    return {
      status: 'error',
      errorCode: 'routing_failed',
      message: 'No suitable facilitator found for the requested network/asset combination',
      details: { preferredNetworks, preferredAssets },
    };
  }

  // ==========================================================================
  // ERC-8004 Trust Scoring (logged for tuning, low impact on routing)
  // ==========================================================================
  const erc8004TrustScore = scoreErc8004Trust(input.erc8004);
  const compositeRouting = computeCompositeRoutingScore(1.0, input.erc8004); // Base 1.0 for now
  const routingMode = input.mode || 'live';
  
  // Get facilitator URL for logging
  const facilitatorUrl = selection.facilitator.config.healthCheckUrl;
  
  // Log routing decision with per-facilitator context
  logRoutingDecision({
    facilitatorId: selection.facilitatorId,
    facilitatorUrl,
    network: selection.network,
    asset: selection.asset,
    mode: routingMode,
    strategy: input.facilitatorStrategy || 'auto',
    agentId: input.agentId,
    agentType: input.agentType,
    useCase: input.useCase,
    resource: input.resource,
    erc8004Score: erc8004TrustScore,
  });
  
  logger.info({
    component: 'X402Router',
    facilitatorId: selection.facilitatorId,
    facilitatorUrl,
    scheme: selection.scheme,
    network: selection.network,
    asset: selection.asset,
    strategy: input.facilitatorStrategy || 'auto',
    mode: routingMode,
    erc8004: {
      present: !!input.erc8004,
      agentId: input.erc8004?.agentId,
      trustTier: input.erc8004?.trustTier,
      reputationScore: input.erc8004?.reputationScore,
      computedTrustScore: erc8004TrustScore,
      compositeScore: compositeRouting.compositeScore,
      erc8004Weight: compositeRouting.erc8004Weight,
    },
    msg: 'Selected facilitator for x402 job',
  });

  // ==========================================================================
  // Step 3: Create Route Record (non-critical - don't fail if DB unavailable)
  // ==========================================================================
  let routeId: string = crypto.randomUUID(); // Fallback ID if DB create fails
  let dbRouteCreated = false;
  
  try {
    const route = await createRoute({
      request_id: crypto.randomUUID(),
      correlation_id: agentId ? `agent:${agentId}` : null,
      client_id: walletId || null,
      agent_id: agentId || null,
      network: selection.network,
      token: selection.asset,
      amount: input.maxSpend || '0',
      selected_facilitator_id: selection.facilitatorId,
      status: 'verifying',
    });
    routeId = route.id;
    dbRouteCreated = true;
  } catch (error) {
    // Route recording is non-critical - log and continue with execution
    logger.warn({
      error: error instanceof Error ? error.message : 'Unknown error',
      fallbackRouteId: routeId,
      msg: 'Failed to create route record in DB, continuing with execution',
    });
  }

  // ==========================================================================
  // Step 4: Execute Job
  // ==========================================================================
  const result = await executeX402Job(selection, input, routeId);

  // Update route status based on result (only if DB route was created)
  if (dbRouteCreated) {
    try {
      if (result.status === 'success') {
        await updateRouteStatus(routeId, 'settled', new Date().toISOString());
      } else {
        await updateRouteStatus(routeId, 'failed', new Date().toISOString());
      }
    } catch (error) {
      logger.warn({
        routeId,
        error: error instanceof Error ? error.message : 'Unknown error',
        msg: 'Failed to update route status',
      });
    }
  }

  // Log completion with per-facilitator context
  logRouteComplete({
    facilitatorId: selection.facilitatorId,
    facilitatorUrl,
    network: selection.network,
    asset: selection.asset,
    success: result.status === 'success',
    errorCode: result.status === 'error' ? result.errorCode : undefined,
    errorMessage: result.status === 'error' ? result.message : undefined,
    httpStatus: result.status === 'success' ? result.httpStatus : undefined,
    amountPaid: result.status === 'success' ? result.amountPaid : undefined,
    mode: routingMode,
    agentType: input.agentType,
    useCase: input.useCase,
    resourceUrl: input.resource,
  });
  
  logger.info({
    component: 'X402Router',
    routeId,
    facilitatorId: selection.facilitatorId,
    facilitatorUrl,
    status: result.status,
    errorCode: result.status === 'error' ? result.errorCode : null,
    msg: 'x402 job execution completed',
  });

  return result;
}

