import { NextRequest, NextResponse } from 'next/server';
import { routeAndExecuteX402Job } from '@/smf/router';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'RouterExecuteEndpoint' });

const corsHeaders = {
  'Access-Control-Allow-Origin': '*',
  'Access-Control-Allow-Methods': 'POST, OPTIONS',
  'Access-Control-Allow-Headers': 'Content-Type',
};

// Default to 'demo' mode - SMF mode requires explicit opt-in
const ROUTER_MODE = process.env.X402_ROUTER_MODE ?? 'demo';

export async function OPTIONS() {
  return new NextResponse(null, { status: 200, headers: corsHeaders });
}

export async function POST(req: NextRequest) {
  let body: any = null;
  
  try {
    body = await req.json();
  } catch (parseError) {
    logger.warn({ error: parseError instanceof Error ? parseError.message : 'Unknown', msg: 'Failed to parse request body' });
    return NextResponse.json(
      {
        ok: false,
        mode: ROUTER_MODE,
        error: 'Invalid JSON body',
      },
      { status: 200, headers: corsHeaders } // Return 200 to avoid retry loops
    );
  }

  // Demo mode - just acknowledge the request
  if (ROUTER_MODE === 'demo') {
    return NextResponse.json(
      {
        ok: true,
        mode: 'demo',
        message: 'NexFlow router received your request.',
        received: body,
      },
      { status: 200, headers: corsHeaders }
    );
  }

  // SMF mode - execute the job
  try {
    const result = await routeAndExecuteX402Job(body);
    return NextResponse.json(
      {
        ok: true,
        mode: 'smf',
        result,
      },
      { status: 200, headers: corsHeaders }
    );
  } catch (error) {
    const errorMessage = error instanceof Error ? error.message : String(error);
    const errorStack = error instanceof Error ? error.stack : undefined;
    
    logger.error({
      error: errorMessage,
      stack: errorStack,
      body: JSON.stringify(body).substring(0, 500),
      msg: 'Router execution failed with uncaught exception',
    });
    
    // Return 200 with error details to avoid retry loops and agent failures
    // The error is in the response body, not the HTTP status
    return NextResponse.json(
      {
        ok: false,
        mode: 'smf',
        error: 'Router execution failed',
        details: errorMessage,
      },
      { status: 200, headers: corsHeaders } // Always 200 to prevent agent retries
    );
  }
}
